<template>
      <header>
        <div class="logo"  @click="switchComponent('main')"> <img :src="`img/my-pic.png`" alt="Alaa Mhna"></div>
        <div class="name" @click="switchComponent('main')">Alaa Mhna</div>
        <div class="search">
          <input type="text" placeholder="Search" :class="[active_input? 'active':'']">
          <i class="fas fa-search" @click="active_input = !active_input"></i>
        </div>
      </header>
</template>
<style lang="scss" scoped>
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&family=Tajawal:wght@500&display=swap');
  header{  
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      padding:10px;
      position: absolute;
      top: 0;
      background-color: #ffffff14;
      border-bottom: 1px solid #ffffff08;
      width: 100%;
      z-index: 5;
      .logo{
        cursor: pointer;
        width: 45px;
        img{
          width: 100%;
          border-radius: 50%;
        }
      
      }
      .name{
        cursor: pointer;
        font-family: 'Dancing Script', cursive;
        font-size: 30px;
        color: #fff;
        margin-inline-start: 10px;
        @media (max-width:568px) {
        width: 85%;
        text-align: end;
        }
      }
      .search{
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: end;
        @media (max-width:568px) {
          justify-content: space-between;
          padding: 10px 0;
          display: none;
        }
        i{
          cursor: pointer;
          color: #fff;
          font-size: 20px;
          margin-inline-start: 15px;
          margin-inline-end: 10px;
        }
        input{
          outline: none;
          width: 0;
          background-color: transparent;
          border: none;
          transition: .5s;
          font-weight: bold;
          border-radius: 5px;
        }
        input.active{
          width:200px;
          padding: 10px;
          background-color:#fff;
        }
      }
    }
</style>
<script>
export default {
  name: "header",
  data() {
    return {
      active_input : false,
    };
  },

  props: [],
  components: { 
    },
  computed: {},
  methods: {
    switchComponent(comName){
      localStorage.setItem("switch_com",comName);
      this.$router.go()
    }
  },
   mounted() {
  
  }
};
</script>
